.bodyContainer {
  height: 20px;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

li {
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  font-size: 20px;
}
.button {
  display: -ms-flexbox;
  display: flex;
  border-radius: 25px;
  background-color: dimgrey;
  width: 50px;
  height: 50px;
  margin: 5px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.button:hover {
  background-color: lightslategrey;
}

.button:active {
  background-color: lightgrey;
}

.big {
  width: 112px;
}

.operand {
  background-color: orange;
}

.operand:hover {
  background-color: darkorange;
}

.operand:active {
  background-color: orangered;
}

.operand-selected {
  background-color: #fff;
  color: orange;
}

.operand-selected:hover {
  background-color: #ddd;
}

.operand-selected:active {
  background-color: #aaa;
}



